.sectionTitle {
  position: relative;
  height: 45vh;
  width: 100vw;
  margin-bottom: 10vmin;
  &::after {
    @include absolute;
    @include pseudo($background: rgba(0, 0, 0, 0.466));
  }
  img {
    @extend %back-img;
    z-index: 1;
  }
  &_child {
    @include absolute(9%, 65%, 0, 50%);
    width: 100%;
    height: auto;
    overflow: hidden;
    z-index: 2;
    h1 {
      font-size: calc(100 * var(--res));
      font-weight: 600;
      @extend %title;
      text-transform: capitalize;
      &.active {
        transform: translateY(0);
        transition-delay: 1s;
      }
      &.no_results {
        font-size: calc(110 * var(--res));
        color: #000;
        margin: 10vh auto;
      }
    }
  }
}
.sectionBody {
  margin-bottom: 5vmin;
}
