.search_results {
  position: relative;
  width: 100vw;
  margin: auto;

  .color_instruction {
    position: absolute;
    right: 2%;
    bottom: 5%;
    width: 40%;
    font-weight: 500;
    font-size: calc(25 * var(--res));
    border-radius: 5px;
    background: rgb(46, 230, 77);
    color: black;
    padding: 0.5vmin 1vmin;
    z-index: 3;
    .triangle {
      @include absolute($top: 100%, $y: 0%);
      height: 0;
      width: 0;
      border-top: 1vmin solid rgb(46, 230, 77);
      border-left: 1vmin solid transparent;
      border-right: 1vmin solid transparent;
    }
  }
  .search_results_body {
    position: relative;
    @include flex(row, space-between, flex-start);
    width: 98%;
    margin: auto;
    .search_results_body_child {
      width: 70%;
    }

    &_child .no_results h1 {
      font-size: calc(105 * var(--res));
      font-weight: 500;
      margin: 5vh auto;
    }
    &_child .no_results div {
      position: relative;
      width: 70%;
      margin: 5vmin 0;
      font-weight: 500;
      font-size: calc(35 * var(--res));
      border-radius: 10px;
      background: rgb(46, 230, 77);
      padding: 3vmin;
      box-shadow: $box-shadow;
      .triangle {
        @include absolute($top: 20%, $left: -2vmin, $x: 0%);
        height: 0;
        width: 0;
        border-bottom: 2vmin solid transparent;
        border-top: 2vmin solid transparent;
        border-right: 2vmin solid rgb(46, 230, 77);
      }
    }
  }
}

@media screen and (max-width: $tablet-max-width) {
  .search_results .search_results_body {
    @include flex(column, flex-start, center);
    width: 100%;
    &_child {
      width: 85%;
    }
  }
}
