@import "../../scss/_mixins";
@import "../../scss/_placeholders";
@import "../../scss/_variables";
.parent {
  width: 30vw;
  height: 30vw;
  @include flex;
  background-color: rgb(210, 210, 210);
  margin: 10vh auto;
  .child {
    position: relative;
    width: 50%;
    height: 50%;
    border: 3px solid $red;
    border-radius: 50%;
    transform: rotate(0deg);
    transition: transform 3s ease-in-out;
    div {
      position: absolute;
      top: -10%;
      left: -10%;
      width: 70%;
      height: 70%;
      background-color: rgb(210, 210, 210);
    }
    &:hover {
      transform: rotate(1360deg);
    }
  }
}
