.get_product {
  width: 90vw;

  h2 {
    @include flex;
    width: 85%;
    font-weight: 500;
    padding: 2vmin 0vmin;
    height: auto;
    margin: auto;
    color: $red;
  }
  .getProductErrMsg {
    width: 85%;
    @include flex;
    height: auto;
    margin: auto;
    padding: 2vmin 0vmin;
    border-radius: 5px;
    color: red;
    background: rgba(255, 5, 5, 0.268);
    border: 2px solid rgb(251, 54, 54);
  }
}
