@mixin flex(
  $flex-direction: row,
  $justify-content: center,
  $align-items: center
) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}
@mixin grid($grid-template-columns, $grid-template-rows) {
  display: grid;
  grid-template-columns: $grid-template-columns;
  grid-template-rows: $grid-template-rows;
}
@mixin absolute($left: 50%, $top: 50%, $x: 50%, $y: 50%) {
  position: absolute;
  left: $left;
  top: $top;
  transform: translate(-$x, -$y);
}
@mixin pseudo($height: 100%, $width: 100%, $background: transparent) {
  content: "";
  height: $height;
  width: $width;
  background: $background;
}

//Breakpoint
@mixin mobile {
  @media only screen and (max-width: $mobile-max-width) {
    @content;
  }
}
@mixin tablet {
  @media only screen and (max-width: $tablet-max-width) and (min-width: $mobile-max-width) {
    @content;
  }
}
@mixin desktop {
  @media only screen and (max-width: $desktop-max-width) and (min-width: $tablet-max-width) {
    @content;
  }
}
@mixin large-desktop {
  @media only screen and (min-width: $large-desktop-min-width) {
    @content;
  }
}
