@import './scss/_variables.scss';
*,
::before,
::after {
  box-sizing:border-box;
  margin:0;
  padding:0;
  color:inherit;
  font:inherit;
}
ol,ul{
  list-style-type:none;
}
a{
  cursor:pointer;
  text-decoration: none;
}
.nav-link{
  color:white;
}

//loader animation before window load
 #loader {
  position: fixed;
  top: 0;
  left:0;
  display:flex;
  height:100vh;
  width: 100vw;
  transition:all 1200ms ease;
  background-color: black;
  background-size: cover;
  justify-content: center;
  align-items: center;
  opacity:1;
  z-index: 100;
}
#loader.fondu_out {
  opacity:0;
  z-index: -1;
}
.lettre{
  position:relative;
  margin-right: 3px;
  color:#484848;
  font-size:calc(130 * var(--res));
  letter-spacing:8px;
  
  &:first-child{
    animation: flash 1.2s linear infinite;
    animation-delay: 0.1s;
  }

  &:nth-child(2) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.2s;
  }

  &:nth-child(3) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.3s;
  }

  &:nth-child(4) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.4s;
  }

  &:nth-child(5) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.5s;
  }

  &:nth-child(6) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.6s;
  }

  &:nth-child(7) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.7s;
  }
  &:nth-child(8) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.8s;
  }

  &:nth-child(9) {
    animation: flash 1.2s linear infinite;
    animation-delay: 0.9s;
  }
  
  &:nth-child(10) {
    animation: flash 1.2s linear infinite;
    animation-delay: 1s;
  }
}


@keyframes flash {
  0% {
    color: #fff900;
    text-shadow: 0 0 7px #fff900;
  }
  90%{
    color:#484848;
    text-shadow:none;
  } 
  100%{
    color:#fff900;
    text-shadow: 0 0 7px #fff900;
  }
}

::-webkit-scrollbar{
  width:0;
  background:transparent;
}


.click_scroll_bar{
  position:fixed;
  top:0;
  right:0;
  width:1vw;
  height:100vh;
  background:rgba(170,170,170,0.356);
  user-select:none;
  z-index:9;
  border-radius:15px;
  .scroll_bar{
    position:fixed;
    top:0;
    right:0;
    width:1vw;
    height:0;
    background:linear-gradient(to top, rgb(254,254,5) ,rgb(254,63,3));
    z-index:1;
    border-radius:15px;
  }
}
